<script>
    import { modules, context } from './stores.js';
    import ModuleMovement from './ModuleMovement.svelte';
    import DeleteButton from './DeleteButton.svelte';
    import { createNewId, cvsAllHover, inputsAllHover, unhover, setPosition } from './utils.js';
    import { spring } from 'svelte/motion';
    
    export let state = {
        type: 'vca',
        gain: 1,
        id: createNewId(),
        inputId: null,
        cvId: null,
        title: 'Amplifier'
    };

    $modules[state.id] = {};
    const module = $modules[state.id];
    module.state = state;

    if (!module.state.position) module.state.position = setPosition();
    
    let moduleNode;
    let controlsNode;
    let deleteNode;

    $: if (module.state.inputId != null) {
        module.input = $modules[module.state.inputId];
    } else {
        module.input = null;
    }

    let cvModule;

    $: if (module.state.cvId != null) {
        cvModule = $modules[module.state.cvId];
    } else {
        cvModule = null;
    }

    var gainNode = $context.createGain();

    module.output = gainNode;
    module.cv = gainNode.gain;

    $: gainNode.gain.setValueAtTime(module.state.gain, $context.currentTime);

    var currentInput;

    $: if (!module.destroyed) {
        if (module.input) {
            if (currentInput) currentInput.disconnect(gainNode);
            currentInput = module.input.output;
            currentInput.connect(gainNode);
            if (module.input.input || module.input.inputs) module.input.update();
        } else {
            if (currentInput) currentInput.disconnect(gainNode);
            currentInput = null;
        }
    }

    var currentCvModule;

    $: if (currentCvModule) {
        currentCvModule.setGain(module.state.id, module.state.gain);
    }

    $: if (!module.destroyed) {
        if (cvModule) {
            gainNode.gain.cancelScheduledValues($context.currentTime);
            gainNode.gain.setValueAtTime(0, $context.currentTime);
            if (currentCvModule) {
                if (currentCvModule.outputs[module.state.id]); currentCvModule.removeOutput(module.state.id, module.cv);
            }
            currentCvModule = cvModule;
            if (!currentCvModule.outputs[module.state.id]) currentCvModule.addOutput(module.state.id, module.cv);
        } else {
            gainNode.gain.cancelScheduledValues($context.currentTime);
            gainNode.gain.setValueAtTime(module.state.gain, $context.currentTime);
            if (currentCvModule) {
                if (currentCvModule.outputs[module.state.id]) currentCvModule.removeOutput(module.state.id, module.cv);
            }
            currentCvModule = null;
        }
    }

    function connectCV(e) {
        module.state.cvId = e.target.selectedOptions[0].value;
    }

    module.clearCurrents = () => {
        currentInput = null;
        cvModule = null;
        currentCvModule = null;
    }

    module.update = () => {
        module.input = module.input;
    }

    function setModule(node) {
        moduleNode = node;
    }

    function setControls(node) {
        controlsNode = node;
    }

    function setDelete(node) {
        deleteNode = node;
    }
    
    let opacity = spring(1, {
        stiffness: 0.1,
        damping: 0.5
    });
    let bobSize = spring(0, {
        stiffness: 0.3,
        damping: 0.2
    });

    $: if (moduleNode) moduleNode.style.opacity = `${$opacity}`;

    module.fade = () => {
        opacity.set(0.2);
    }

    module.halfFade = () => {
        opacity.set(0.8)
    }

    module.unfade = () => {
        opacity.set(1);
    }

    module.bob = () => {
        bobSize.set(10);
        setTimeout(() => {
            bobSize.set(0);
        }, 50);
    }

    module.bob();
</script>

<main bind:this={module.component}>
<ModuleMovement bind:moduleNode bind:controlsNode bind:deleteNode nodeSize={{ x: 280, y: 310 }} bind:nodePos={state.position} bind:bobSize />
<div id="module" use:setModule>
    <div class="delete" use:setDelete><DeleteButton module={module} /></div>
    <h1>{module.state.id}</h1>
    <div id="controls" use:setControls>
        <h2 class='editableTitle' bind:textContent={module.state.title} contenteditable='true'>{module.state.title}</h2>
        <div class='inputDiv' on:mouseenter={() => {inputsAllHover(module)}} on:mouseleave={unhover}>
        <label><select bind:value={module.state.inputId}>
        {#each Object.entries($modules) as [id, m]}
            {#if m.output && id != module.state.id}
            <option value={id}>{id} {m.state.title}</option>
            {/if}
        {/each}
        <option value={null}></option>
        </select> Input</label></div>
        <div class='inputDiv' on:mouseenter={() => cvsAllHover(module)} on:mouseleave={unhover}>
        <label><select value={null} on:change={connectCV}>
        {#each Object.entries($modules) as [id, m]}
            {#if m.state.type == 'adsr' || m.state.type == 'lfo'}
            <option value={id}>{id} {m.state.title}</option>
            {/if}
        {/each}
        <option value={null}></option>
        </select> Control</label></div><br>
        <label for='gain'>Volume ({module.state.gain.toFixed(2)})</label><input id='gain' bind:value={module.state.gain} type='range' min='0' max='1' step='0.001'>
    </div>
</div>
<br>
</main>

<style>
    #module {
        background-color: #88ff88;
        border-style: solid;
        position: absolute;
        user-select: none;
        border-radius: 50px;
        border-color: #222222;
    }

    select {
        width: 120px;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    .delete {
        position: absolute;
        right: 20px;
        top: 20px;
    }

    .editableTitle {
        width: fit-content;
        min-width: 50px;
        max-width: 90%;
        max-height: 28px;
        margin-left: auto;
        margin-right: auto;
        margin-top: -10px;
        margin-bottom: 10px;
        text-overflow: ellipsis;
        overflow: hidden;
        padding: 10px
    }
</style>

<svelte:window />